                    duration: 2000 // ËÆæÁΩÆÊèêÁ§∫ÊòæÁ§∫Êó∂Èó¥ÔºåÂçï‰ΩçÊòØÊØ´Áßí
                  });
                  this.ipv6Loading = false
                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // ËÆæÁΩÆÊèêÁ§∫ÊòæÁ§∫Êó∂Èó¥ÔºåÂçï‰ΩçÊòØÊØ´Áßí
                  });
                }
              })
              .catch(error => {
                console.error('Error:', error)
              })
              .finally(() => {
                this.getInstanceCfgInfo()
              })
        }
      } catch (cancelError) {
        // Áî®Êà∑ÁÇπÂáª‰∫Ü "ÂèñÊ∂à" ÊàñÂÖ≥Èó≠‰∫ÜÂºπÊ°ÜÔºå‰∏çÈúÄË¶ÅÊâßË°å‰ªª‰ΩïÊìç‰Ωú
        ElMessage({
          message: 'Êìç‰ΩúÂ∑≤ÂèñÊ∂à',
          type: 'info',
          duration: 2000,
        });
      }
    },
  },
};
</script>

